// ticket status
const TICKET_STATUS_IN_PROGRESS = '';
const TICKET_STATUS_PAUSE = '';
const TICKET_STATUS_VALIDATE = '';
const TICKET_STATUS_CLOSE = '';
const TICKET_STATUS_ABNORMAL_CLOSE = '';
const TICKET_STATUS_INVALID = '';

const IN_PROGRESS_TICKET_TOTAL_HANDLE_TIME = '';

const YES = '';
const NO = '';

function initProjectRate() {
  let map = {};
  for (let secondCategory of [...DEFAULT_SECOND_CATEGORIES, KEY_TICKET_TOTAL]) {
    map[secondCategory] = {};
    for (let itemName of RATE_STATISTIC_ITEMS) {
      map[secondCategory][itemName] = 0;
    }
  }
  return map;
}

function initProject() {
  let map = {};
  for (let secondCategory of [...DEFAULT_SECOND_CATEGORIES, KEY_TICKET_TOTAL]) {
    map[secondCategory] = {};
    for (let itemName of DEFAULT_STATISTIC_ITEMS) {
      map[secondCategory][itemName] = 0;
    }
  }
  return map;
}

function isInProgress(ticketStatus) {
  return ticketStatus.indexOf(TICKET_STATUS_IN_PROGRESS) === 0;
}

// 
function statistic(data, closeTimeStart, closeTimeEnd) {
  // statistic data
  let dataMap = {};

  for (let ticket of data) {
    let projectName = filterProjectName(ticket[KEY_PROJECT_NAME]);

    if (dataMap[projectName] == null) {
      dataMap[projectName] = initProject();
    }
    let secondCategory = ticket[KEY_SECOND_CATEGORY];

    let ticketStatistic = dataMap[projectName][secondCategory];

    let ticketStatus = ticket[KEY_TICKET_STATUS];
    let ticketCloseTime = moment(ticket[KEY_CLOSE_TIME]);

    //  =  +  +  + 
    if (isInProgress(ticketStatus) ||
      ticketStatus === TICKET_STATUS_ABNORMAL_CLOSE ||
      ticketStatus === TICKET_STATUS_CLOSE || ticketStatus === TICKET_STATUS_PAUSE) {
      ticketStatistic[TICKET_ACCEPT]++;
    }

    //  =  +  +  + 
    if (isInProgress(ticketStatus) || ticketStatus == TICKET_STATUS_PAUSE ||
      ((ticketStatus === TICKET_STATUS_ABNORMAL_CLOSE || ticketStatus === TICKET_STATUS_CLOSE) &&
        ticketCloseTime > closeTimeEnd)) {
      ticketStatistic[TICKET_OPEN]++;
    }

    //  = 
    if (isInProgress(ticketStatus) && ticketStatus.indexOf(TICKET_STATUS_VALIDATE) > 0) {
      ticketStatistic[TICKET_VALIDATE]++;
    }

    //  = 
    if (ticketStatus === TICKET_STATUS_PAUSE) {
      ticketStatistic[TICKET_PAUSE]++;
    }

    //  = 
    if (ticketStatus == TICKET_STATUS_INVALID) {
      ticketStatistic[TICKET_INVALID]++;
    }

    //  =  + 
    if ((ticketStatus === TICKET_STATUS_CLOSE || ticketStatus === TICKET_STATUS_ABNORMAL_CLOSE) &&
      (ticketCloseTime >= closeTimeStart && ticketCloseTime <= closeTimeEnd)) {
      ticketStatistic[TICKET_CLOSE]++;
    }

    //  = 
    if (ticketStatus === TICKET_STATUS_CLOSE &&
      (ticketCloseTime >= closeTimeStart && ticketCloseTime <= closeTimeEnd)) {
      ticketStatistic[TICKET_CLOSE_NORMAL]++;
    }

    //  = 
    if (ticketStatus === TICKET_STATUS_ABNORMAL_CLOSE &&
      (ticketCloseTime >= closeTimeStart && ticketCloseTime <= closeTimeEnd)) {
      ticketStatistic[TICKET_CLOSE_ABNORMAL]++;
    }

    //  =  + 
    if ((ticketStatus === TICKET_STATUS_CLOSE ||
      ticketStatus === TICKET_STATUS_ABNORMAL_CLOSE) && ticket[KEY_IS_PROCESS_EXCCED] === NO) {
      if (ticketCloseTime >= closeTimeStart && ticketCloseTime <= closeTimeEnd) {
        ticketStatistic[TICKET_CLOSE_ON_TIME]++;
      }
    }

    // 0.5h) =  +  +  + 
    // 30h) =  +  +  + 
    if (isInProgress(ticketStatus) ||
      ticketStatus === TICKET_STATUS_ABNORMAL_CLOSE ||
      ticketStatus === TICKET_STATUS_CLOSE || ticketStatus === TICKET_STATUS_PAUSE) {
      let { day, hour, min } = parseTime(ticket[KEY_FIRST_RESPONSE_TIME]);
      if (day * 24 * 60 + hour * 60 + min <= 30) {
        ticketStatistic[TICKET_RESPONSE_IN_30_MIN]++
      }
      if (day * 24 * 60 + hour * 60 + min <= 30 * 60) {
        ticketStatistic[TICKET_RESPONSE_IN_30_HOUR]++;
      }
    }

    //  =  + 
    if (ticketStatus === TICKET_STATUS_CLOSE || ticketStatus === TICKET_STATUS_ABNORMAL_CLOSE) {
      let ticketCloseTime = moment(ticket[KEY_CLOSE_TIME]);
      if (ticketCloseTime >= closeTimeStart && ticketCloseTime <= closeTimeEnd) {
        let { day, hour, min } = parseTime(ticket[KEY_PROCESS_TIME]);
        ticketStatistic[TICKET_CLOSE_PROCESS_TIME] += (day + hour / 24 + min / 60 / 24);
      }
    }

    //  =  +  +  + 
    if (isInProgress(ticketStatus) || ticketStatus === TICKET_STATUS_PAUSE ||
      ((ticketStatus === TICKET_STATUS_ABNORMAL_CLOSE || ticketStatus === TICKET_STATUS_CLOSE) &&
        ticketCloseTime > closeTimeEnd)) {
      let { day, hour, min } = parseTime(ticket[KEY_PROCESS_TIME]);
      ticketStatistic[TICKET_OPEN_PROCESS_TIME] += (day + hour / 24 + min / 60 / 24);
    }

    //  =  +  +  +  + 
    if (isInProgress(ticketStatus) || ticketStatus === TICKET_STATUS_ABNORMAL_CLOSE ||
      ticketStatus === TICKET_STATUS_CLOSE || ticketStatus === TICKET_STATUS_PAUSE ||
      ticketStatus == TICKET_STATUS_INVALID) {
      ticketStatistic[TICKET_SYSTEM_COUNT]++;
    }
  }

  for (let projectName in dataMap) {
    // get total for all second categories
    for (let secondCategory of DEFAULT_SECOND_CATEGORIES) {
      for (let statisticItem of DEFAULT_STATISTIC_ITEMS) {
        dataMap[projectName][KEY_TICKET_TOTAL][statisticItem] += dataMap[projectName][secondCategory][statisticItem];
      }
    }

    // get average process time
    for (let secondCategory in dataMap[projectName]) {
      let ticketCloseProcessTime = dataMap[projectName][secondCategory][TICKET_CLOSE_PROCESS_TIME];
      let ticketCloseCount = dataMap[projectName][secondCategory][TICKET_CLOSE];
      dataMap[projectName][secondCategory][TICKET_CLOSE_PROCESS_TIME] =
        ticketCloseCount > 0 ? Number(ticketCloseProcessTime / ticketCloseCount).toFixed(2) : 0;

      let ticketOpenProcessTime = dataMap[projectName][secondCategory][TICKET_OPEN_PROCESS_TIME];
      let ticketOpenCount = dataMap[projectName][secondCategory][TICKET_OPEN];
      dataMap[projectName][secondCategory][TICKET_OPEN_PROCESS_TIME] =
        ticketOpenCount > 0 ? Number(ticketOpenProcessTime / ticketOpenCount).toFixed(2) : 0;
    }
  }
  return dataMap;
}

