function mergeStageResult(resultArray, filterFunc) {
  let mergeResult = {};
  resultArray.forEach(function (result) {
    for (let projectName in result) {
      let filteredProjectName = filterFunc(projectName);
      if (mergeResult[filteredProjectName] == null) {
        mergeResult[filteredProjectName] = initProject();
      }
      for (let secondCategory of [...DEFAULT_SECOND_CATEGORIES, KEY_TICKET_TOTAL]) {
        for (let item of DEFAULT_STATISTIC_ITEMS) {
          if (item === TICKET_CLOSE_PROCESS_TIME) {
            let ticketCloseCount = result[projectName][secondCategory][TICKET_CLOSE];
            let ticketCloseProcessTime = result[projectName][secondCategory][TICKET_CLOSE_PROCESS_TIME];
            mergeResult[filteredProjectName][secondCategory][TICKET_CLOSE_PROCESS_TIME] +=
              ticketCloseCount * ticketCloseProcessTime;
          } else if (item === TICKET_OPEN_PROCESS_TIME) {
            let ticketOpenProcessTime = result[projectName][secondCategory][TICKET_OPEN_PROCESS_TIME];
            let ticketOpenCount = result[projectName][secondCategory][TICKET_OPEN];
            mergeResult[filteredProjectName][secondCategory][TICKET_OPEN_PROCESS_TIME] +=
              ticketOpenCount * ticketOpenProcessTime;
          }
          else {
            mergeResult[filteredProjectName][secondCategory][item] +=
              result[projectName][secondCategory][item];
          }
        }
      }
    }
  });
  // get average process time
  for (let projectName in mergeResult) {
    for (let secondCategory in mergeResult[projectName]) {
      let ticketCloseProcessTime = mergeResult[projectName][secondCategory][TICKET_CLOSE_PROCESS_TIME];
      let ticketCloseCount = mergeResult[projectName][secondCategory][TICKET_CLOSE];
      mergeResult[projectName][secondCategory][TICKET_CLOSE_PROCESS_TIME] =
        ticketCloseCount > 0 ? Number(ticketCloseProcessTime / ticketCloseCount).toFixed(2) : 0;

      let ticketOpenProcessTime = mergeResult[projectName][secondCategory][TICKET_OPEN_PROCESS_TIME];
      let ticketOpenCount = mergeResult[projectName][secondCategory][TICKET_OPEN];
      mergeResult[projectName][secondCategory][TICKET_OPEN_PROCESS_TIME] =
        ticketOpenCount > 0 ? Number(ticketOpenProcessTime / ticketOpenCount).toFixed(2) : 0;
    }
  }
  return mergeResult;
}

