function statisticCity(data, startDate, endDate) {
  // 
  let periodNewAddedTickets = data.filter((item) => {
    let reportTime = moment(item[KEY_REPORT_TIME]);
    let secondCategory = item[KEY_SECOND_CATEGORY];
    return secondCategory != null && reportTime >= startDate && reportTime <= endDate;
  });
  let periodNewAddedResult = statistic(periodNewAddedTickets, startDate, endDate);

  let yearStartDate = moment(startDate).set({ month: 0, date: 1, hour: 0, minute: 0, second: 0 });
  let thisYearbeforePeriodTickets = data.filter((item) => {
    let reportTime = moment(item[KEY_REPORT_TIME]);
    let secondCategory = item[KEY_SECOND_CATEGORY];
    return secondCategory != null && reportTime < startDate && reportTime >= yearStartDate;
  });

  let thisYearBeforePeriodResult = statisticThisYearBeforePeriodTickets(thisYearbeforePeriodTickets,
    startDate, endDate);

  let thisYearBeforePeriodTotalResult = statisticThisYearBeforeStartDateTotalTickets(
    thisYearbeforePeriodTickets, yearStartDate, endDate);

  let thisYearTotalTickets = data.filter((item) => {
    let reportTime = moment(item[KEY_REPORT_TIME]);
    let secondCategory = item[KEY_SECOND_CATEGORY];
    return secondCategory != null && reportTime <= endDate && reportTime >= yearStartDate;
  });
  let thisYearTotalResult = statisticThisYearTotalTickets(thisYearTotalTickets,
    yearStartDate, endDate);

  let lastYearBeforePeriodTickets = data.filter((item) => {
    let reportTime = moment(item[KEY_REPORT_TIME]);
    let secondCategory = item[KEY_SECOND_CATEGORY];
    return secondCategory != null && reportTime < yearStartDate;
  });
  let lastYearBeforePeriodResult = statisticLastYearBeforePeriodTickets(lastYearBeforePeriodTickets,
    startDate, endDate);
  
  let lastYearBeforePeriodTotalResult = statisticLastYearBeforePeriodTotalTickets(lastYearBeforePeriodTickets,
    yearStartDate, endDate);

  let beforePeriodNeedHandleResult = {};
  [periodNewAddedResult, thisYearBeforePeriodResult, lastYearBeforePeriodResult].forEach(function (result) {
    for (let projectName in result) {
      if (beforePeriodNeedHandleResult[projectName] == null) {
        beforePeriodNeedHandleResult[projectName] = initProject();
      }
      for (let secondCategory of [...DEFAULT_SECOND_CATEGORIES, KEY_TICKET_TOTAL]) {
        for (let item of DEFAULT_STATISTIC_ITEMS) {
          if (item === TICKET_CLOSE_PROCESS_TIME) {
            let ticketCloseCount = result[projectName][secondCategory][TICKET_CLOSE];
            let ticketCloseProcessTime = result[projectName][secondCategory][TICKET_CLOSE_PROCESS_TIME];
            beforePeriodNeedHandleResult[projectName][secondCategory][TICKET_CLOSE_PROCESS_TIME] +=
              ticketCloseCount * ticketCloseProcessTime;
          } else if (item === TICKET_OPEN_PROCESS_TIME) {
            let ticketOpenProcessTime = result[projectName][secondCategory][TICKET_OPEN_PROCESS_TIME];
            let ticketOpenCount = result[projectName][secondCategory][TICKET_OPEN];
            beforePeriodNeedHandleResult[projectName][secondCategory][TICKET_OPEN_PROCESS_TIME] +=
              ticketOpenCount * ticketOpenProcessTime;
          }
          else {
            beforePeriodNeedHandleResult[projectName][secondCategory][item] +=
              result[projectName][secondCategory][item];
          }
        }
      }
    }
  });
  // get average process time
  for (let projectName in beforePeriodNeedHandleResult) {
    for (let secondCategory in beforePeriodNeedHandleResult[projectName]) {
      let ticketCloseProcessTime = beforePeriodNeedHandleResult[projectName][secondCategory][TICKET_CLOSE_PROCESS_TIME];
      let ticketCloseCount = beforePeriodNeedHandleResult[projectName][secondCategory][TICKET_CLOSE];
      beforePeriodNeedHandleResult[projectName][secondCategory][TICKET_CLOSE_PROCESS_TIME] =
        ticketCloseCount > 0 ? Number(ticketCloseProcessTime / ticketCloseCount).toFixed(2) : 0;

      let ticketOpenProcessTime = beforePeriodNeedHandleResult[projectName][secondCategory][TICKET_OPEN_PROCESS_TIME];
      let ticketOpenCount = beforePeriodNeedHandleResult[projectName][secondCategory][TICKET_OPEN];
      beforePeriodNeedHandleResult[projectName][secondCategory][TICKET_OPEN_PROCESS_TIME] =
        ticketOpenCount > 0 ? Number(ticketOpenProcessTime / ticketOpenCount).toFixed(2) : 0;
    }
  }

  let periodTotalResult = {};
  [lastYearBeforePeriodTotalResult, thisYearTotalResult].forEach(function (result) {
    for (let projectName in result) {
      if (periodTotalResult[projectName] == null) {
        periodTotalResult[projectName] = initProject();
      }
      for (let secondCategory of [...DEFAULT_SECOND_CATEGORIES, KEY_TICKET_TOTAL]) {
        for (let item of DEFAULT_STATISTIC_ITEMS) {
          if (item === TICKET_CLOSE_PROCESS_TIME) {
            let ticketCloseCount = result[projectName][secondCategory][TICKET_CLOSE];
            let ticketCloseProcessTime = result[projectName][secondCategory][TICKET_CLOSE_PROCESS_TIME];
            periodTotalResult[projectName][secondCategory][TICKET_CLOSE_PROCESS_TIME] +=
              ticketCloseCount * ticketCloseProcessTime;
          } else if (item === TICKET_OPEN_PROCESS_TIME) {
            let ticketOpenProcessTime = result[projectName][secondCategory][TICKET_OPEN_PROCESS_TIME];
            let ticketOpenCount = result[projectName][secondCategory][TICKET_OPEN];
            periodTotalResult[projectName][secondCategory][TICKET_OPEN_PROCESS_TIME] +=
              ticketOpenCount * ticketOpenProcessTime;
          }
          else {
            periodTotalResult[projectName][secondCategory][item] +=
              result[projectName][secondCategory][item];
          }
        }
      }
    }
  });
  // get average process time
  for (let projectName in periodTotalResult) {
    for (let secondCategory in periodTotalResult[projectName]) {
      let ticketCloseProcessTime = periodTotalResult[projectName][secondCategory][TICKET_CLOSE_PROCESS_TIME];
      let ticketCloseCount = periodTotalResult[projectName][secondCategory][TICKET_CLOSE];
      periodTotalResult[projectName][secondCategory][TICKET_CLOSE_PROCESS_TIME] =
        ticketCloseCount > 0 ? Number(ticketCloseProcessTime / ticketCloseCount).toFixed(2) : 0;

      let ticketOpenProcessTime = periodTotalResult[projectName][secondCategory][TICKET_OPEN_PROCESS_TIME];
      let ticketOpenCount = periodTotalResult[projectName][secondCategory][TICKET_OPEN];
      periodTotalResult[projectName][secondCategory][TICKET_OPEN_PROCESS_TIME] =
        ticketOpenCount > 0 ? Number(ticketOpenProcessTime / ticketOpenCount).toFixed(2) : 0;
    }
  }

  let stageRateResult = {};
  statisticRate(periodNewAddedResult, NEW_ADD_CLOSE_RATE, stageRateResult);
  statisticRate(thisYearBeforePeriodResult, REMAIN_CLOSE_RATE, stageRateResult);
  statisticRate(beforePeriodNeedHandleResult, OVERALL_CLOSE_RATE, stageRateResult);
  statisticRate(periodNewAddedResult, RESPONSE_IN_30_MIN_RATE, stageRateResult);
  statisticRate(periodNewAddedResult, RESPONSE_IN_30_HOUR_RATE, stageRateResult);
  statisticRate(beforePeriodNeedHandleResult, CLOSE_ON_TIME_RATE, stageRateResult);
  statisticRate(periodTotalResult, TOTAL_OVERALL_CLOSE_RATE, stageRateResult);
  
  let stageResult = {
    periodNewAddedResult: periodNewAddedResult,
    thisYearBeforePeriodResult: thisYearBeforePeriodResult,
    thisYearBeforePeriodTotalResult: thisYearBeforePeriodTotalResult,
    lastYearBeforePeriodResult: lastYearBeforePeriodResult,
    lastYearBeforePeriodTotalResult: lastYearBeforePeriodTotalResult,
    beforePeriodNeedHandleResult: beforePeriodNeedHandleResult,
    thisYearTotalResult: thisYearTotalResult,
    periodTotalResult: periodTotalResult,
    rateResult: stageRateResult
  };

  // 
  let projectResult = {
    periodNewAddedResult:  mergeStageResult([periodNewAddedResult], filterProjectStage),
    thisYearBeforePeriodResult: mergeStageResult([thisYearBeforePeriodResult], filterProjectStage),
    thisYearBeforePeriodTotalResult: mergeStageResult([thisYearBeforePeriodTotalResult], filterProjectStage),
    thisYearTotalResult: mergeStageResult([thisYearTotalResult], filterProjectStage),
    lastYearBeforePeriodResult: mergeStageResult([lastYearBeforePeriodResult], filterProjectStage),
    lastYearBeforePeriodTotalResult: mergeStageResult([lastYearBeforePeriodTotalResult], filterProjectStage),
    beforePeriodNeedHandleResult: mergeStageResult([beforePeriodNeedHandleResult], filterProjectStage),
    periodTotalResult: mergeStageResult([periodTotalResult], filterProjectStage)
  };
  let projectRateResult = {}
  statisticRate(projectResult.periodNewAddedResult, NEW_ADD_CLOSE_RATE, projectRateResult);
  statisticRate(projectResult.thisYearBeforePeriodResult, REMAIN_CLOSE_RATE, projectRateResult);
  statisticRate(projectResult.beforePeriodNeedHandleResult, OVERALL_CLOSE_RATE, projectRateResult);
  statisticRate(projectResult.periodNewAddedResult, RESPONSE_IN_30_MIN_RATE, projectRateResult);
  statisticRate(projectResult.periodNewAddedResult, RESPONSE_IN_30_HOUR_RATE, projectRateResult);
  statisticRate(projectResult.beforePeriodNeedHandleResult, CLOSE_ON_TIME_RATE, projectRateResult);
  statisticRate(projectResult.periodTotalResult, TOTAL_OVERALL_CLOSE_RATE, projectRateResult);
  projectResult.rateResult = projectRateResult;

  let cityResult = {
    periodNewAddedResult:  mergeProjectResult([projectResult.periodNewAddedResult]),
    thisYearBeforePeriodResult: mergeProjectResult([projectResult.thisYearBeforePeriodResult]),
    thisYearBeforePeriodTotalResult: mergeProjectResult([projectResult.thisYearBeforePeriodTotalResult]),
    thisYearTotalResult: mergeProjectResult([projectResult.thisYearTotalResult]),
    lastYearBeforePeriodResult: mergeProjectResult([projectResult.lastYearBeforePeriodResult]),
    lastYearBeforePeriodTotalResult: mergeProjectResult([projectResult.lastYearBeforePeriodTotalResult]),
    beforePeriodNeedHandleResult: mergeProjectResult([projectResult.beforePeriodNeedHandleResult]),
    periodTotalResult: mergeProjectResult([projectResult.periodTotalResult])
  };
  let cityRateResult = {}
  statisticCityRate(cityResult.periodNewAddedResult, NEW_ADD_CLOSE_RATE, cityRateResult);
  statisticCityRate(cityResult.thisYearBeforePeriodResult, REMAIN_CLOSE_RATE, cityRateResult);
  statisticCityRate(cityResult.beforePeriodNeedHandleResult, OVERALL_CLOSE_RATE, cityRateResult);
  statisticCityRate(cityResult.periodNewAddedResult, RESPONSE_IN_30_MIN_RATE, cityRateResult);
  statisticCityRate(cityResult.periodNewAddedResult, RESPONSE_IN_30_HOUR_RATE, cityRateResult);
  statisticCityRate(cityResult.beforePeriodNeedHandleResult, CLOSE_ON_TIME_RATE, cityRateResult);
  statisticCityRate(cityResult.periodTotalResult, TOTAL_OVERALL_CLOSE_RATE, cityRateResult);
  cityResult.rateResult = cityRateResult;

  let result = {
    stageResult,
    projectResult,
    cityResult
  };
  return result;
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cityData: {
        periodNewAddedData: [],
        thisYearBeforePeriodData: [],
        thisYearBeforePeriodTotalData: [],
        thisYearTotalData: [],
        lastYearBeforePeriodData: [],
        lastYearBeforePeriodTotalData: [],
        beforePeriodNeedHandleData: [],
        periodTotalData: [],
        rateData: [],
        cities: []
      },
      loading: false
    };
  }

  start = (fileList, startDate, endDate) => {
    this.setState({ loading: true });
    readExcels(fileList).then((data) => {
      // clear previous data
      this.setState({
        cityData: {
          periodNewAddedData: [],
          thisYearBeforePeriodData: [],
          thisYearBeforePeriodTotalData: [],
          thisYearTotalData: [],
          lastYearBeforePeriodData: [],
          lastYearBeforePeriodTotalData: [],
          beforePeriodNeedHandleData: [],
          periodTotalData: [],
          rateData: [],
          cities: []
        }
      });
      this.setState({
        projectData: {}
      });
      let allCities = [];
      for (let cityName in data) {
        allCities.push(cityName);
        let result = statisticCity(data[cityName], startDate, endDate);
        this.setState((state) => ({
          cityData: {
            periodNewAddedData:  [...state.cityData.periodNewAddedData, ...formatCityData(result.cityResult.periodNewAddedResult, cityName)],
            thisYearBeforePeriodData: [...state.cityData.thisYearBeforePeriodData, ...formatCityData(result.cityResult.thisYearBeforePeriodResult, cityName)],
            thisYearBeforePeriodTotalData: [...state.cityData.thisYearBeforePeriodTotalData, ...formatCityData(result.cityResult.thisYearBeforePeriodTotalResult, cityName)],
            thisYearTotalData: [...state.cityData.thisYearTotalData, ...formatCityData(result.cityResult.thisYearTotalResult, cityName)],
            lastYearBeforePeriodData: [...state.cityData.lastYearBeforePeriodData, ...formatCityData(result.cityResult.lastYearBeforePeriodResult, cityName)],
            lastYearBeforePeriodTotalData: [...state.cityData.lastYearBeforePeriodTotalData, ...formatCityData(result.cityResult.lastYearBeforePeriodTotalResult, cityName)],
            beforePeriodNeedHandleData: [...state.cityData.beforePeriodNeedHandleData, ...formatCityData(result.cityResult.beforePeriodNeedHandleResult, cityName)],
            periodTotalData: [...state.cityData.periodTotalData, ...formatCityData(result.cityResult.periodTotalResult, cityName)],
            rateData: [...state.cityData.rateData, ...formatToCityRateTable(result.cityResult.rateResult, cityName)],
            cities: allCities
          }
        }));
        this.setState(state => ({
          projectData: {
            ...state.projectData,
            [cityName]: result.projectResult
          }
        }));
      }

      this.setState({
        loading: false
      });
    });
  }

  exportExcel = () => {
    let header = [KEY_PROJECT_NAME, KEY_STATISTIC_ITEM, ...DEFAULT_SECOND_CATEGORIES, KEY_TICKET_TOTAL];
    if (this.state.newAddedData.length > 0 || this.state.thisYearBeforeStartData.length > 0 ||
      this.state.thisYearBeforePeriodTotalData.length > 0 || this.state.thisYearTotalData.length > 0 ||
      this.state.lastYearBeforePeriodData.length > 0 ||
      this.state.lastYearBeforePeriodTotalData.length > 0 ||
      this.state.beforePeriodNeedHandleData.length > 0 ||
      this.state.periodTotalData.length > 0) {
      let sheet1 = XLSX.utils.json_to_sheet(this.state.periodNewAddedData, {
        header: header
      });
      let sheet2 = XLSX.utils.json_to_sheet(this.state.thisYearBeforePeriodData, {
        header: header
      });
      let sheet3 = XLSX.utils.json_to_sheet(this.state.thisYearBeforePeriodTotalData, {
        header: header
      });
      let sheet4 = XLSX.utils.json_to_sheet(this.state.thisYearTotalData, {
        header: header
      });
      let sheet5 = XLSX.utils.json_to_sheet(this.state.lastYearBeforePeriodData, {
        header: header
      });
      let sheet6 = XLSX.utils.json_to_sheet(this.state.lastYearBeforePeriodTotalData, {
        header: header
      });
      let sheet7 = XLSX.utils.json_to_sheet(this.state.beforePeriodNeedHandleData, {
        header: header
      });
      let sheet8 = XLSX.utils.json_to_sheet(this.state.periodTotalData, {
        header: header
      });
      let sheet9 = XLSX.utils.json_to_sheet(this.state.rateData, {
        header: header
      });
      let workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, sheet1, STATISTIC_PERIOD_ADD);
      XLSX.utils.book_append_sheet(workbook, sheet2, STATISTIC_BEFORE_PERIOD_REMAIN);
      XLSX.utils.book_append_sheet(workbook, sheet3, STATISTIC_BEFORE_PERIOD_REMAIN_TOTAL);
      XLSX.utils.book_append_sheet(workbook, sheet4, STATISTIC_THIS_YEAR_TOTAL);
      XLSX.utils.book_append_sheet(workbook, sheet5, STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN);
      XLSX.utils.book_append_sheet(workbook, sheet6, STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN_TOTAL);
      XLSX.utils.book_append_sheet(workbook, sheet7, STATISTIC_BEFORE_PERIOD_NEED_HANDLE);
      XLSX.utils.book_append_sheet(workbook, sheet8, STATISTIC_BEFORE_PERIOD_TOTAL);
      XLSX.utils.book_append_sheet(workbook, sheet9, STATISTIC_PERIOD_RATE)
      XLSX.writeFile(workbook, 'statistic-' + moment(Date.now()).format('YYYY-MM-DD-HH-MM-SS') + '.xlsx')
    } else {
      message.error('');
    }
  }

  render() {

    const tabCity = (
      <CityView
        periodNewAddedData={this.state.cityData.periodNewAddedData}
        thisYearBeforePeriodData={this.state.cityData.thisYearBeforePeriodData}
        thisYearBeforePeriodTotalData={this.state.cityData.thisYearBeforePeriodTotalData}
        thisYearTotalData={this.state.cityData.thisYearTotalData}
        lastYearBeforePeriodData={this.state.cityData.lastYearBeforePeriodData}
        lastYearBeforePeriodTotalData={this.state.cityData.lastYearBeforePeriodTotalData}
        beforePeriodNeedHandleData={this.state.cityData.beforePeriodNeedHandleData}
        periodTotalData={this.state.cityData.periodTotalData}
        rateData={this.state.cityData.rateData}
        cities={this.state.cityData.cities}
      />
    );

    const tabProject = (
      <ProjectView
        cities={this.state.cityData.cities}
        projectData={this.state.projectData}
      />
    );
 
    return (
      <LocaleProvider locale={zh_CN}>
        <div>
          <h2 className="text--center heading"></h2>
          <DataForm
            onStart={this.start}
            onExport={this.exportExcel}
            loading={this.state.loading}
          />
          <Row style={{ margin: '40px 0' }}>
            <Col span={22} offset={1}>
              <Tabs type="card">
                {/* <Tabs.TabPane tab="" key=""></Tabs.TabPane> */}
                <Tabs.TabPane tab="" key="">{tabCity}</Tabs.TabPane>
                <Tabs.TabPane tab="" key="">{tabProject}</Tabs.TabPane>
                <Tabs.TabPane tab="" key=""></Tabs.TabPane>
              </Tabs>
            </Col>
          </Row>
        </div>
      </LocaleProvider>
    );
  }
}

export default App;
