import React from 'react';
import { Row, Col, Form, Select } from 'antd';
import ProjectTable from './ProjectTable';
import {
  STATISTIC_PERIOD_ADD,
  STATISTIC_BEFORE_PERIOD_REMAIN,
  STATISTIC_BEFORE_PERIOD_REMAIN_TOTAL,
  STATISTIC_THIS_YEAR_TOTAL,
  STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN,
  STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN_TOTAL,
  STATISTIC_BEFORE_PERIOD_NEED_HANDLE,
  STATISTIC_BEFORE_PERIOD_TOTAL,
  STATISTIC_PERIOD_RATE
} from '../constants';
import {
  formatToTable,
  formatToRateTable
} from '../utils';

class ProjectView extends React.Component {
  static defaultProps = {
    cities: [],
    projectData: {}
  }

  constructor(props) {
    super(props);
    this.state = {
      projects: [],
      data: {}
    }
  }

  filterCity = (city) => {
    if (this.props.projectData[city] != null) {
      this.setState({
        data: this.props.projectData[city]
      });
      let projects = [];
      for (let item in this.props.projectData[city]) {
        for (let project in this.props.projectData[city][item]) {
          projects = [...projects, project];
        }
      }
      this.setState({
        projects: projects
      });
    }
  }

  filterProjects = (projects) => {
    if (projects.length > 0) {
      this.setState({
        filteredProjects: projects
      });
    } else {
      this.setState({
        filteredProjects: []
      });
    }
  }

  render() {
    return (
      <div>
        <Form>
          <Row>
            <Col span={16}>
              <Form.Item
                label=''
                labelCol={{ span: 4 }}
                wrapperCol={{ span: 16 }}
              >
                <Select
                  style={{ width: '100%' }}
                  onChange={this.filterCity}
                >
                  {
                    this.props.cities.map((city) => (
                      <Select.Option key={city}>{city}</Select.Option>
                    ))
                  }
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={16}>
              <Form.Item
                label=''
                labelCol={{ span: 4 }}
                wrapperCol={{ span: 16 }}
              >
                <Select
                  style={{ width: '100%' }}
                  onChange={this.filterProjects}
                >
                  {
                    this.state.projects.map((project) => (
                      <Select.Option key={project}>{project}</Select.Option>
                    ))
                  }
                </Select>
              </Form.Item>
            </Col>
          </Row>
        </Form>
        <Row>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_PERIOD_ADD}
              data={formatToTable(this.state.data.periodNewAddedData)}
              filteredProjects={[]}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_BEFORE_PERIOD_REMAIN}
              data={formatToTable(this.state.data.thisYearBeforePeriodData)}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_BEFORE_PERIOD_REMAIN_TOTAL}
              data={formatToTable(this.state.data.thisYearBeforePeriodTotalData)}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_THIS_YEAR_TOTAL}
              data={formatToTable(this.state.data.thisYearTotalData)}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN}
              data={formatToTable(this.state.data.lastYearBeforePeriodData)}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN_TOTAL}
              data={formatToTable(this.state.data.lastYearBeforePeriodTotalData)}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_BEFORE_PERIOD_NEED_HANDLE}
              data={formatToTable(this.state.data.beforePeriodNeedHandleData)}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_BEFORE_PERIOD_TOTAL}
              data={formatToTable(this.state.data.periodTotalData)}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <ProjectTable
              title={STATISTIC_PERIOD_RATE}
              data={formatToRateTable(this.state.data.rateData)}
              filteredCities={this.state.filteredCities}
            />
          </div>
        </Row>
      </div>
    );
  }
}

export default ProjectView;
